import React from "react";
import {
  Panel,
  PanelHeader,
  Header,
  Button,
  Group,
  Cell,
  Div,
} from "@vkontakte/vkui";
import bridge from "@vkontakte/vk-bridge";
import "./style.css";

function loading() {
  setTimeout(
    () =>
      (document.getElementById("child").innerText = "  ..."),
    1500
  );
  setTimeout(
    () =>
      (document.getElementById("child").innerText = " ..."),
    2700
  );
  setTimeout(
    () => (document.getElementById("child").innerText = " ..."),
    5000
  );
  setTimeout(
    () =>
      (document.getElementById("child").innerText =
        "  .."),
    6000
  );
  setTimeout(
    () =>
      (document.getElementById("child").innerText = " ,.."),
    7300
  );
  setTimeout(
    () => (document.getElementById("child").innerText = " -..."),
    8300
  );
  setTimeout(() => {
    var a = document.getElementById("button");
    a.style.display = "inline";

    document.getElementById("child").style.display = "none";
  }, 9000);
}

const Deploy = ({ id, go, fetchedUser }) => (
  <Panel id={id} className="Panel">
    <Div className="subscrib">
      <Div className="child">
        <img
          src={"https://svgsilh.com/svg/1293387.svg"}
          height={150}
          width={150}
        />
      </Div>
      <Div
        id="child"
        className="child"
        style={{
          fontWeight: "bolder",
          fontSize: "2.5em",
        }}
      >
         ...
      </Div>
      <Div id="button">
        <h1> </h1>
        <h5>  ,      !</h5>
        <Button
          stretched
          size="l"
          mode="Overlay Primary"
          onClick={() => {
            go("finish");
          }}
        >
           
        </Button>
      </Div>
      <Cell onClick={loading(go)} />
    </Div>
  </Panel>
);

export default Deploy;
